import React from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import jrxw from 'assets/report/jrxw.png';

const FinancialBehavior = Styled.div`
    padding-top: 0.5334rem;
    display: flex;
    flex-direction: column;
`;

FinancialBehavior.Grade = Styled.div`
    padding: 0 1.0667rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    span {
        color: #5B5B5B;
        font-size: 0.3734rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        em {
            margin-top: 0.08rem;
            font-size: 0.7467rem;
            color: ${({ score }) => {
    if (score > 80) {
      return "#E73B11";
    } else if (score > 60) {
      return "#E6A200";
    } else if (score > 40) {
      return "#5CAA07";
    } else {
      return "#4EA0FF";
    }
  }};
        }
    }

    .sp {
        width: 0.0267rem;
        height: 1.28rem;
        background-color: #D8D8D8;
    }
`;

FinancialBehavior.Progress = Styled.div`
    margin-top: 0.4267rem;

    .pC {
        height: 0.32rem;
        background-color: #D8D8D8;
    }

    .grD {
        margin-top: 0.08rem;
        color: #9B9B9B;
        font-size: 0.32rem;
        display: flex;
        justify-content: space-between;

        span {
            color: #E73B11;
        }
    }
`;

FinancialBehavior.Progress.Highlight = Styled.div`
    width: ${({ score }) => {
    return `${score}%`;
  }};
    height: 100%;
    background-color: ${({ score }) => {
    if (score > 80) {
      return "#E73B11";
    } else if (score > 60) {
      return "#E6A200";
    } else if (score > 40) {
      return "#5CAA07";
    } else {
      return "#4EA0FF";
    }
  }};
`;

FinancialBehavior.Tip = Styled.p`
    margin-top: 0.2667rem;
    padding: 0 1.6rem;
    color: #B8B5B5;
    font-size: 0.32rem;
    line-height: 0.4534rem;
    text-align: center;
`;

FinancialBehavior.Read = Styled.div`
    padding-bottom: 0.5334rem;
    color: #8E8E8E;
    font-size: 0.32rem;
    line-height: 0.4534rem;
    border-top: 1px solid #E6E7EA;

    .T {
        padding: 0.2667rem 0 0.08rem;
        color: #8E8E8E;
        font-size: 0.3734rem;
        line-height: 0.5334rem;
    }

    li {
        padding-left: 0.16rem;
        position: relative;

        &:before {
            content: '';
            left: 0;
            position: absolute;
        }
    }
`;

FinancialBehavior.Statistics = Styled.div`
    .T {
        padding: 0.2134rem 0.2667rem;
        color: #4EA0FF;
        font-size: 0.3734rem;
        border-top: 1px solid #E6E6E6;  
        display: flex;
        justify-content: space-between;

        span {
            color: #9B9B9B;
            font-size: 0.32rem;
        }
    }

    ul {
        padding: 0.24rem 0.2667rem 0.32rem;

        li {
            margin-top: 0.2667rem;
            color: #8E8E8E;
            font-size: 0.3734rem;
            line-height: 0.64rem;
            display: flex;
            justify-content: space-between;

            &:first-of-type {
                margin-top: 0;
            }

            span {
                color: #5B5B5B;
            }
        }
    }
`;

function getLevel(score) {
  if (score > 80) {
    return "";
  } else if (score > 60) {
    return "";
  } else if (score > 40) {
    return "";
  } else if (score > 20) {
    return "";
  } else {
    return "";
  }
}

export default class extends React.PureComponent {
  render() {
    const score = 85;
    return (
      <Section>
        <Section.Title>
          <img
            src={jrxw}
            alt="JRXW"
            onClick={() => false}
          />
          
        </Section.Title>
        <FinancialBehavior>
          <div style={{ padding: "0 0.2667rem" }}>
            <FinancialBehavior.Grade score={score}>
              <span>
                
                <em className="sC">{score}</em>
              </span>
              <div className="sp" />
              <span>
                
                <em className="lvL">{getLevel(score)}</em>
              </span>
            </FinancialBehavior.Grade>
            <FinancialBehavior.Progress>
              <div className="pC">
                <FinancialBehavior.Progress.Highlight score={score} />
              </div>
              <div className="grD">
                <span></span>
              </div>
            </FinancialBehavior.Progress>
            <FinancialBehavior.Tip>
              
            </FinancialBehavior.Tip>
            <div style={{ marginTop: "0.5334rem" }}>
              <Section.Item>
                <span>8</span>
              </Section.Item>
              <Section.Item>
                <span>10</span>
              </Section.Item>
              <Section.Item>
                <span>5</span>
              </Section.Item>
              <Section.Item>
                <span className="sCe">58</span>
              </Section.Item>
              <Section.Item>
                <span className="sCe">58</span>
              </Section.Item>
              <FinancialBehavior.Read>
                <p className="T"></p>
                <ul>
                  <li></li>
                  <li>P2P</li>
                </ul>
              </FinancialBehavior.Read>
            </div>
          </div>
          <FinancialBehavior.Statistics>
            <div className="T">
              <span>24</span>
            </div>
            <ul>
              <li>
                <span>8</span>
              </li>
              <li>
                <span>2</span>
              </li>
              <li>
                <span>5</span>
              </li>
              <li>
                <span>2</span>
              </li>
              <li>
                <span>2</span>
              </li>
              <li>
                <span>1</span>
              </li>
              <li>
                <span>1</span>
              </li>
              <li>
                <span>2</span>
              </li>
              <li>
                <span>4</span>
              </li>
              <li>
                <span>27</span>
              </li>
            </ul>
          </FinancialBehavior.Statistics>
          <FinancialBehavior.Statistics>
            <div className="T"></div>
            <ul>
              <li>
                3<span>7</span>
              </li>
              <li>
                6<span>8</span>
              </li>
              <li>
                12<span>9</span>
              </li>
              <li>
                <span>5</span>
              </li>
              <li>
                <span>6</span>
              </li>
              <li>
                <span>7503.58</span>
              </li>
            </ul>
          </FinancialBehavior.Statistics>
        </FinancialBehavior>
      </Section>
    );
  }
}
