import React, { PureComponent } from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import grtz from 'assets/report/grtz.png';

const PersonalBehavior = Styled.div`
    padding: 0.5334rem;
`;

PersonalBehavior.Tags = Styled.ul`
    margin-top: -0.2134rem;
    color: #4EA0FF;
    font-size: 0.3734rem;
    line-height: 0.5334rem;
    display: flex;
    flex-wrap: wrap;

    li {
        padding: 0.0534rem 0.32rem;
        margin-top: 0.2134rem;
        margin-right: 0.2134rem;
        border: 1px solid #DBDCDF;
        border-radius: 2px;
        position: relative;

        &:before {
            content: "";
            width: 0.0534rem;
            height: 0.0534rem;
            top: 0.0534rem;
            left: 0.0534rem;
            background-color: #4EA0FF;
            border-radius: 100%;
            display: block;
            position: absolute;
        }
    }
`;

PersonalBehavior.Tips = Styled.p`
    margin-top: 0.5334rem;
    color: #B8B5B5;
    font-size: 0.32rem;
    text-align: center;
`;

export default class extends PureComponent {
  render() {
    return (
      <Section>
        <Section.Title>
          <img
            src={grtz}
            alt="GRTZ"
            onClick={() => false}
          />
          
        </Section.Title>
        <PersonalBehavior>
          <PersonalBehavior.Tags>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </PersonalBehavior.Tags>
          <PersonalBehavior.Tips>
            
          </PersonalBehavior.Tips>
        </PersonalBehavior>
      </Section>
    );
  }
}
