import Styled from "styled-components";

const Companys = Styled.div`
  padding: 0.2667rem 0 0.5334rem;
`;

Companys.Container = Styled.div`
    padding: 0 0.2667rem;
`;

Companys.ArrowTitle = Styled.div`
    width: 1.4934rem;
    height: 0.64rem;
    margin: 0.32rem 0 0.2667rem;
    padding: 0 0.2667rem;
    color: #4EA0FF;
    font-size: 0.4267rem;
    background-color: #FFFFFF;
    border-top: 0.0267rem solid #E6E7EA;
    border-bottom: 0.0267rem solid #E6E7EA;
    display: flex;
    align-items: center;
    position: relative;

    &:before, &:after {
        content: '';
        top: -0.03rem;
        right: -0.62rem;
        border-width: 0.32rem;
        border-style: solid;
        border-color: transparent transparent transparent #E6E7EA;
        position: absolute;
        z-index: 9;
    }

    &:before {
        right: -0.59rem;
        border-color: transparent transparent transparent #FFFFFF;
        z-index: 10;
    }
`;

Companys.EmptyText = Styled.p`
  color: #5B5B5B;
  font-size: 0.3734rem;
`;

export default Companys;