import React from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import bggs from 'assets/report/bggs.png';
import hit from 'assets/report/hit.png';
import miss from 'assets/report/miss.png';

const ReportSummary = Styled.div`
    padding: 0.32rem 0.2667rem;
    color: #5B5B5B;
    font-size: 0.32rem;
    line-height: 0.4534rem;

    p {
        margin-top: 0.2134rem;
        display: flex;
        align-items: center;

        &:first-of-type {
          margin-top: 0;
        }
    }

    img {
        height: 0.4267rem;
        margin-right: 0.2134rem;
        display: block;
    }
`;

export default class extends React.PureComponent {
  render() {
    return (
      <Section>
        <Section.Title>
          <div>
            <div>
              <img
                src={bggs}
                alt="BGGS"
                onClick={() => false}
              />
              
            </div>
            <span>
              <em>3</em>
            </span>
          </div>
        </Section.Title>
        <ReportSummary>
          <p>
            <img
              src={hit}
              alt="hit"
              onClick={() => false}
            />
            
          </p>
          <p>
            <img
              src={hit}
              alt="hit"
              onClick={() => false}
            />
            
          </p>
          <p>
            <img
              src={hit}
              alt="hit"
              onClick={() => false}
            />
            
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            24
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            3.6
          </p>
          <p>
            <img
              src={miss}
              alt="hit"
              onClick={() => false}
            />
            {parseFloat(3.6 * 1.25).toFixed(1)}
          </p>
        </ReportSummary>
      </Section>
    );
  }
}
