import React, { PureComponent } from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import grbl from 'assets/report/grbl.png';

const PersonalBadInfo = Styled.div`
    padding: 0.3734rem 0.2667rem;
    color: #5B5B5B;
    font-size: 0.3734rem;
    line-height: 0.5334rem;

    & > div {
        margin-top: 0.24rem;
        padding-bottom: 0.1334rem;
        border-bottom: 1px solid #E6E7EA;

        &:first-of-type {
            margin-top: 0;
        }

        &:last-of-type {
            border-bottom: 0;
        }

        p {
            margin-top: 0.1334rem;
        }

        div {
            margin-top: 0.32rem;

            &:first-of-type {
              margin-top: 0;
            }
        }
 
        .T {
            margin-top: 0;
            padding-bottom: 0.1067rem;
            color: #4EA0FF;
        }

        span {
            color: #8E8E8E;
        }
    }
`;

export default class extends PureComponent {
  render() {
    return (
      <Section>
        <Section.Title>
          <img
            src={grbl}
            alt="GRBL"
            onClick={() => false}
          />
          
        </Section.Title>
        <PersonalBadInfo>
          <div>
            <p className="T"></p>
            <div>
              <p>
                <span></span>
                
                  </p>
              <p>****</p>
              <p>
                <span></span>
                2017
                  </p>
              <p>
                <span></span>
                
                  </p>
            </div>
          </div>
          <div>
            <p className="T"></p>
            <div>
              <p>**</p>
              <p>
                <span></span>
                2019
              </p>
              <p>
                <span></span>
                50000
              </p>
            </div>
          </div>
          <div>
            <p className="T"></p>
            <p></p>
          </div>
          <div>
            <p className="T"></p>
            <p></p>
          </div>
          <div>
            <p className="T"></p>
            <div>
              <p>*******</p>
              <p>
                <span></span>
                2018/05/08
              </p>
            </div>
          </div>
          <div>
            <p className="T"></p>
            <div>
              <p>***</p>
              <p>
                <span></span>
                2017/05/08
              </p>
            </div>
          </div>
        </PersonalBadInfo>
      </Section>
    );
  }
}
