import React, { PureComponent } from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import xyzz from 'assets/report/xyzz.png';

const CreditIndex = Styled.div`
  padding: 0.9067rem 1.28rem 0.48rem;

  & > div {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .sp {
      width: 0.0267rem;
      height: 1.76rem;
      background-color: #D8D8D8;
    }
  }
  
  p {
    margin-top: 0.5867rem;
    color: #B8B5B5;
    font-size: 0.32rem;
    text-align: center;
  }
`;

CreditIndex.Level = Styled.div`
  color: #B7B7B7;
  font-size: 0.3734rem;
  display: flex;
  flex-direction: column;

  span {
    font-size: 0.7467rem;
  }
`;

function getGrade(score) {
  if (score > 79) {
    return {
      color: "#4EA0FF",
      text: ""
    };
  } else if (score > 59) {
    return {
      color: "#5CAA07",
      text: ""
    };
  } else if (score > 39) {
    return {
      color: "#FFDA3B",
      text: ""
    };
  } else if (score > 9) {
    return {
      color: "#E6A200",
      text: ""
    };
  } else {
    return {
      color: "#E73B11",
      text: ""
    };
  }
}

export default class extends PureComponent {
  state = {
    score: 90
  };
  componentDidMount() {
    const { score } = this.state;
    const { color } = getGrade(score);
    const ctx = this.refs.canvas.getContext("2d");
    // 
    const devicePixelRatio = window.devicePixelRatio || 1;
    // canvas
    const backingStoreRatio =
      ctx.webkitBackingStorePixelRatio ||
      ctx.mozBackingStorePixelRatio ||
      ctx.msBackingStorePixelRatio ||
      ctx.oBackingStorePixelRatio ||
      ctx.backingStorePixelRatio ||
      1;
    // canvas
    const ratio = devicePixelRatio / backingStoreRatio;
    const { clientWidth } = document.documentElement || document.body;
    const D = clientWidth * 0.352 * ratio;
    const R = D / 2;
    this.refs.canvas.width = D;
    this.refs.canvas.height = clientWidth * 0.2667 * ratio;
    this.refs.canvas.style.zoom = 1 / ratio;
    // grey outer circle.
    const oC = clientWidth * 0.016 * ratio;
    const sAngle = 0.82;
    const aLength = 2.18 - sAngle;
    ctx.beginPath();
    ctx.lineWidth = oC;
    ctx.strokeStyle = "#CDCDCD";
    ctx.arc(R, R, R - oC, 0.82 * Math.PI, 2.18 * Math.PI);
    ctx.stroke();
    // highlight outer circle.
    ctx.beginPath();
    ctx.lineWidth = oC;
    ctx.strokeStyle = color;
    ctx.arc(
      R,
      R,
      R - oC,
      0.82 * Math.PI,
      (sAngle + aLength * (score / 100)) * Math.PI
    );
    ctx.stroke();
    // separator.
    for (let i = 8; i < 38; i++) {
      const x = ((2 * Math.PI) / 360) * 8 * i;
      ctx.beginPath();
      ctx.strokeStyle = "#FFFFFF";
      ctx.lineWidth = clientWidth * 0.0053 * ratio;
      ctx.moveTo(R, R);
      ctx.lineTo(
        (R / ratio + (Math.sin(x) * R) / ratio) * ratio,
        (R / ratio + (Math.cos(x) * R) / ratio) * ratio
      );
      ctx.stroke();
    }
    // inner grey circle.
    ctx.beginPath();
    ctx.lineWidth = clientWidth * 0.0053 * ratio;
    ctx.strokeStyle = "#CDCDCD";
    ctx.arc(
      R,
      R,
      R - clientWidth * 0.04 * ratio,
      0.82 * Math.PI,
      2.18 * Math.PI
    );
    ctx.stroke();
    // inner highlight circle.
    ctx.beginPath();
    ctx.lineWidth = clientWidth * 0.0053 * ratio;
    ctx.strokeStyle = color;
    ctx.arc(
      R,
      R,
      R - clientWidth * 0.04 * ratio,
      0.82 * Math.PI,
      (sAngle + aLength * (score / 100)) * Math.PI
    );
    ctx.stroke();
    // point.
    const startAngle = 302.4;
    const arcLength = startAngle - 57.6;
    ctx.beginPath();
    ctx.lineWidth = clientWidth * 0.00267 * ratio;
    ctx.strokeStyle = color;
    ctx.arc(
      (R / ratio +
        (Math.sin(
          ((2 * Math.PI) / 360) * (startAngle - arcLength * (score / 100))
        ) *
          (R - clientWidth * 0.04 * ratio)) /
          ratio) *
        ratio,
      (R / ratio +
        (Math.cos(
          ((2 * Math.PI) / 360) * (startAngle - arcLength * (score / 100))
        ) *
          (R - clientWidth * 0.04 * ratio)) /
          ratio) *
        ratio,
      clientWidth * 0.008 * ratio,
      0,
      2 * Math.PI
    );
    ctx.fillStyle = "#FFFFFF";
    ctx.fill();
    ctx.stroke();
    // score.
    ctx.font = `${clientWidth * 0.09067 * ratio}px Helvetica`;
    ctx.fillStyle = color;
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillText(
      score,
      R - clientWidth * 0.02134 * ratio,
      R - clientWidth * 0.0134 * ratio
    );
    // score unit.
    let distance = clientWidth * 0.05867 * ratio;
    if (+score === 0) {
      distance = clientWidth * 0.03734 * ratio;
    } else if (+score === 100) {
      distance = clientWidth * 0.08 * ratio;
    }
    ctx.font = `${clientWidth * 0.04267 * ratio}px Helvetica`;
    ctx.fillStyle = color;
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillText("", R + distance, R);
    // text.
    ctx.font = `${clientWidth * 0.032 * ratio}px Helvetica`;
    ctx.fillStyle = color;
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillText("", R, R + clientWidth * 0.0667 * ratio);
  }
  render() {
    const { color, text } = getGrade(this.state.score);
    return (
      <Section>
        <Section.Title>
          <img
            src={xyzz}
            alt="XYZZ"
            onClick={() => false}
          />
          
        </Section.Title>
        <CreditIndex>
          <div>
            <canvas ref="canvas" />
            <div className="sp" />
            <CreditIndex.Level style={{ color }}>
              
              <span>{text}</span>
            </CreditIndex.Level>
          </div>
          <p>
            
          </p>
        </CreditIndex>
      </Section>
    );
  }
}
