import React, { PureComponent } from "react";
import Styled from "styled-components";
import { AreaChart, XAxis, YAxis, Area } from "recharts";
import Section from "components/common/Section";
import cfpg from 'assets/report/cfpg.png';

const IncomeAssessment = Styled.div`
    padding-top: 0.2667rem;
`;

IncomeAssessment.Chart = Styled(AreaChart)`
  margin-top: 0.32rem;
  margin-left: auto;
  margin-right: auto;

  svg {
    font-size: 0.3467rem;
    overflow: initial;
  }
`;

const year = new Date().getFullYear();

export default class extends PureComponent {
  state = {
    data: [
      { year, income: parseFloat(this.props.value * 1.25).toFixed(1) },
      {
        year: year + 1,
        income: parseFloat(this.props.value * 1.25 * 2).toFixed(1)
      },
      {
        year: year + 2,
        income: parseFloat(this.props.value * 1.25 * Math.sqrt(7)).toFixed(1)
      },
      {
        year: year + 3,
        income: parseFloat(this.props.value * 1.25 * Math.sqrt(10)).toFixed(1)
      },
      {
        year: year + 4,
        income: parseFloat(this.props.value * 1.25 * Math.sqrt(13)).toFixed(1)
      }
    ]
  };
  render() {
    const { clientWidth } = document.documentElement || document.body;
    const XAxisTick = ({ x, y, payload }) => (
      <g transform={`translate(${x},${y})`}>
        <text x={0} y={clientWidth * 0.032} textAnchor="middle" fill="#B8B5B5">
          {payload.value}
        </text>
      </g>
    );
    const YAxisTick = ({ x, y, payload }) =>
      +payload.value !== 0 ? (
        <g transform={`translate(${x},${y})`}>
          <text
            x={-clientWidth * 0.02667}
            y={0}
            textAnchor="middle"
            fill="#B8B5B5"
          >
            {payload.value}
          </text>
        </g>
      ) : (
        ""
      );
    const Label = ({ x, y, value }) => (
      <text
        x={x}
        y={y - clientWidth * 0.016}
        fill="#4EA0FF"
        fontSize={clientWidth * 0.032}
        textAnchor="middle"
      >
        {value}
      </text>
    );
    return (
      <Section>
        <Section.Title>
          <img
            src={cfpg}
            alt="cfpg"
            onClick={() => false}
          />
          
        </Section.Title>
        <IncomeAssessment>
          <Section.Item
            style={{
              marginLeft: "0.2667rem",
              marginRight: "0.2667rem",
              borderBottom: "1px solid #E6E7EA"
            }}
          >
            
            <span>{parseFloat(this.props.value * 1.25).toFixed(1)}</span>
          </Section.Item>
          <IncomeAssessment.Chart
            width={clientWidth * 0.925}
            height={clientWidth * 0.64}
            data={this.state.data}
            margin={{
              top: clientWidth * 0.032,
              bottom: clientWidth * 0.032,
              right: clientWidth * 0.064
            }}
          >
            <XAxis
              dataKey="year"
              interval={0}
              tickLine={false}
              tick={<XAxisTick />}
            />
            <YAxis tickLine={false} interval={0} tick={<YAxisTick />} />
            <Area
              type="monotone"
              dataKey="income"
              stroke="#4EA0FF"
              fillOpacity={1}
              fill="#CEE3FB"
              dot={true}
              label={<Label />}
            />
          </IncomeAssessment.Chart>
        </IncomeAssessment>
      </Section>
    );
  }
}
