import React, { PureComponent } from "react";
import Styled from "styled-components";
import Section from "components/common/Section";
import xwfx from 'assets/report/xwfx.png';

const RiskAppraisal = Styled.div`
    padding: 0.5334rem 0.2667rem;
`;

RiskAppraisal.Score = Styled.div`
    color: #5B5B5B;
    font-size: 0.4267rem;

    em {
        color: ${({ score }) => {
    if (score > 75) {
      return "#E73B11";
    } else if (score > 50) {
      return "#E6A200";
    } else {
      return "#5CAA07";
    }
  }};
    }

    span {
        color: #B8B5B5;
        font-size: 0.32rem;
    }
`;

RiskAppraisal.Progress = Styled.div`
    width: 8.3734rem;
    margin: 1.28rem auto 0.5334rem;
    display: flex;
    position: relative;
`;

RiskAppraisal.Progress.Arrow = Styled.div`
    top: -0.4267rem;
    left: ${({ score }) => score || 0}%;
    margin-left: -0.2667rem;
    border-width: 0.2934rem 0.2934rem 0;
    border-style: solid;
    border-color: ${({ score }) => {
    if (score > 75) {
      return "#E73B11";
    } else if (score > 50) {
      return "#E6A200";
    } else {
      return "#5CAA07";
    }
  }} transparent transparent;
    position: absolute;
`;

RiskAppraisal.Progress.Block = Styled.div`
    font-size: 0.32rem;
    display: flex;
    flex-direction: column;

    div {
        padding: 0.0534rem 0;
        color: #FFFFFF;
        text-align: center;
    }

    span {
        margin-top: 0.16rem;
        margin-right: -0.08rem;
        color: #5B5B5B;    
        align-self: flex-end;
    }
`;

RiskAppraisal.Progress.Low = Styled(RiskAppraisal.Progress.Block)`
    flex-grow: 1;

    div {
        background-color: ${({ actived }) =>
    !!actived ? "#5CAA07" : "#CCCCCC"}
    }
`;

RiskAppraisal.Progress.Medium = Styled(RiskAppraisal.Progress.Block)`
    flex-basis: 25%;
    margin-left: 0.08rem;

    div {
        background-color: ${({ actived }) =>
    !!actived ? "#E6A200" : "#CCCCCC"}
    }
`;

RiskAppraisal.Progress.High = Styled(RiskAppraisal.Progress.Block)`
    flex-basis: 25%;
    margin-left: 0.08rem;

    div {
        background-color: ${({ actived }) =>
    !!actived ? "#E73B11" : "#CCCCCC"}
    }
`;

RiskAppraisal.Tip = Styled.p`
    width: 100%;
    padding-top: 0.2667rem;
    color: #B8B5B5;
    font-size: 0.32rem;
    text-align: center;
    border-top: 1px solid #E6E7EA;
`;

export default class extends PureComponent {
  render() {
    const score = 85;
    return (
      <Section>
        <Section.Title>
          <img
            src={xwfx}
            alt="XWFX"
            onClick={() => false}
          />
          
        </Section.Title>
        <RiskAppraisal>
          <RiskAppraisal.Score score={score}>
            <em>{score}</em>
            <span></span>
          </RiskAppraisal.Score>
          <RiskAppraisal.Progress>
            <RiskAppraisal.Progress.Arrow score={score} />
            <RiskAppraisal.Progress.Low actived={score < 51}>
              <div></div>
              <span>50</span>
            </RiskAppraisal.Progress.Low>
            <RiskAppraisal.Progress.Medium actived={score > 50 && score <= 75}>
              <div></div>
              <span>75</span>
            </RiskAppraisal.Progress.Medium>
            <RiskAppraisal.Progress.High actived={score > 75}>
              <div></div>
              <span>100</span>
            </RiskAppraisal.Progress.High>
          </RiskAppraisal.Progress>
          <div>
            <Section.Item>
              
              <span></span>
            </Section.Item>
            <Section.Item>
              
              <span></span>
            </Section.Item>
            <Section.Item>
              
              <span className="hiT"></span>
            </Section.Item>
            <Section.Item>
              
              <span></span>
            </Section.Item>
            <Section.Item>
              
              <span></span>
            </Section.Item>
            <Section.Item>
              
              <span className="hiT"></span>
            </Section.Item>
            <Section.Item>
              
              <span></span>
            </Section.Item>
            <Section.Item>
              
              <span></span>
            </Section.Item>
            <Section.Item>
              
              <span className="hiT"></span>
            </Section.Item>
          </div>
          <RiskAppraisal.Tip>
            
          </RiskAppraisal.Tip>
        </RiskAppraisal>
      </Section>
    );
  }
}
