import Styled from "styled-components";

const Section = Styled.div`
    margin-bottom: 0.2667rem;
    background-color: #FFFFFF;

    &:last-of-type {
        margin-bottom: 0;
    }
`;

Section.Title = Styled.div`
    padding: 0 0.2667rem;
    color: #4EA0FF;
    font-size: 0.4267rem;
    line-height: 0.96rem;
    border-top: 0.0267rem solid #EAEAEA;
    display: flex;
    align-items: center;

    & > div {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        & > div {
            flex-grow: 1;
            display: flex;
            align-items: center;
        }

        span {
            color: #9B9B9B;
            font-size: 0.32rem;

            em {
                color: #4EA0FF;
            }
        }
    }

    img {
        height: 0.5867rem;
        margin-right: 0.2134rem;
        display: block;
    }
`;

Section.Content = Styled.div`
    padding: 0.2667rem;
`;

Section.Item = Styled.p`
    margin-top: 0.16rem;
    padding: 0.1334rem 0;
    color: #8E8E8E;
    font-size: 0.3734rem;
    border-bottom: 0.0267rem solid #E6E7EA;
    display: flex;
    justify-content: space-between;

    &:last-of-type {
        border-bottom: 0;
    }

    span {
        color: #5B5B5B;

        &.hiT {
            color: #E6A200;
        }

        &.sCe {
            color: #4EA0FF;
        }
    }
`;

Section.ItemTitle = Styled.div`
    padding: 0.4rem 0 0.1067rem;
    color: #4EA0FF;
    font-size: 0.4rem;
`;

Section.InnerItem = Styled.div`
    margin-top: 0.2667rem;
    border-bottom: 0.0267rem solid #E6E7EA;

    &:last-of-type {
        border-bottom: 0;
    }

    p {
        padding-bottom: 0.16rem;
        color: #8E8E8E;
        font-size: 0.3734rem;
        display: flex;
        justify-content: space-between;

        span {
            color: #5B5B5B;

            &.hiT {
                color: #E6A200;
            }
        }
    }
`;

export default Section;
