import React, { PureComponent } from "react";
import Styled from "styled-components";
import avatar from 'assets/report/avatar.png';

const PersonalInfo = Styled.div`
    padding: 0.96rem 0.8rem 0.96rem 1.12rem;
    color: #FFFFFF;
    font-size: 0.32rem;
    background-color: #4EA0FF;
    display: flex;
    justify-content: space-between;
    align-items: center;

    & > div {
      display: flex;
      flex-direction: column;
    }

    .aIC {
      display: flex;
      align-items: center;
    }

    .jCsB {
      width: 100%;
      display: flex;
      justify-content: space-between;
    }

    p {
      line-height: 0.5334rem;
    }
`;

PersonalInfo.Avatar = Styled.div`
    width: 1.387rem;
    height: 1.387rem;
    background-color: #FFFFFF;
    border-radius: 100%;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        border-radius: 100%;
        display: block;
    }
`;

PersonalInfo.Name = Styled.span`
    margin: 0.1067rem 0 0.16rem;
    font-size: 0.4267rem;
    font-weight: 500;
`;

export default class extends PureComponent {
  render() {
    return (
      <PersonalInfo>
        <div className="aIC">
          <PersonalInfo.Avatar>
            <img src={avatar} alt="avatar" onClick={() => false} />
          </PersonalInfo.Avatar>
          <PersonalInfo.Name></PersonalInfo.Name>
          <div className="jCsB">
            <span>30</span>
            <span></span>
          </div>
        </div>
        <div>
          <p></p>
          <p>4403************18</p>
          <p>138******18</p>
          <p>2018110918001130</p>
          <p>2018-11-09</p>
        </div>
      </PersonalInfo>
    );
  }
}
